.custom-spinner {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(12, 30, 91, 0.75);
  z-index: 9998;
  display: flex;
  align-items: center;
  justify-content: center;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  pointer-events: none;
}

.custom-spinner.enter-done {
  opacity: 1;
  pointer-events: visible;
}

.custom-spinner.exit {
  opacity: 0;
}

.custom-spinner-content {
  max-width: 55%;
  min-height: 387px;
  border-radius: 20px;
  background: #ffffff;
  position: fixed;
  /* Shadow Box Iframe */
  box-shadow: 0px 0px 60px rgba(12, 30, 91, 0.35);
  border-radius: 20px;
  transition: all 0.3s ease-in-out;
  transform: translateY(-200px);
  
}

.custom-spinner-content.custom-spinner-small {
  max-width: 30%;
  min-height: 150px;
}

.custom-spinner-close {
  background-color: rgb(253, 253, 253);
  border-radius: 100%;
  width: 35px;
  height: 35px;
  position: absolute;
  top: -3%;
  right: -2%;
  cursor: pointer;
  z-index: 2;
}
.custom-spinner-close.custom-spinner-close-button {
  color: #ff6966;
  cursor: pointer;
}

.custom-spinner-close-button::after {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  content: "\274c"; /* use the hex value here... */
  font-size: 15px;
  line-height: 33px;
}
.custom-spinner.enter-done .custom-spinner-content {
  transform: translateY(0);
}

.custom-spinner.exit .custom-spinner-content {
  transform: translateY(-200px);
}

.lds-default {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.lds-default div {
  position: absolute;
  width: 6px;
  height: 6px;
  background: #fff;
  border-radius: 50%;
  animation: lds-default 1.2s linear infinite;
}
.lds-default div:nth-child(1) {
  animation-delay: 0s;
  top: 37px;
  left: 66px;
}
.lds-default div:nth-child(2) {
  animation-delay: -0.1s;
  top: 22px;
  left: 62px;
}
.lds-default div:nth-child(3) {
  animation-delay: -0.2s;
  top: 11px;
  left: 52px;
}
.lds-default div:nth-child(4) {
  animation-delay: -0.3s;
  top: 7px;
  left: 37px;
}
.lds-default div:nth-child(5) {
  animation-delay: -0.4s;
  top: 11px;
  left: 22px;
}
.lds-default div:nth-child(6) {
  animation-delay: -0.5s;
  top: 22px;
  left: 11px;
}
.lds-default div:nth-child(7) {
  animation-delay: -0.6s;
  top: 37px;
  left: 7px;
}
.lds-default div:nth-child(8) {
  animation-delay: -0.7s;
  top: 52px;
  left: 11px;
}
.lds-default div:nth-child(9) {
  animation-delay: -0.8s;
  top: 62px;
  left: 22px;
}
.lds-default div:nth-child(10) {
  animation-delay: -0.9s;
  top: 66px;
  left: 37px;
}
.lds-default div:nth-child(11) {
  animation-delay: -1s;
  top: 62px;
  left: 52px;
}
.lds-default div:nth-child(12) {
  animation-delay: -1.1s;
  top: 52px;
  left: 62px;
}
@keyframes lds-default {
  0%,
  20%,
  80%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.5);
  }
}
