.login-container {

  background: #FFFFFF;
  box-shadow: 0px 12px 24px -4px rgba(85, 74, 221, 0.1);
  border-radius: 24px;
  padding: 80px 64px;

  .logo-container {
    border: 0px;
    width: 100% !important;
  }

  .login-form-container {
    box-shadow: none;
    /* Inside auto layout */

    .login-content {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: flex-start;
      padding: 0px;
      gap: 30px;
      /* Inside auto layout */

      .form-title {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0px;
        gap: 4px;
        height: 57px;
        /* Inside auto layout */
        flex: none;
        order: 0;
        flex-grow: 0;

        .login-title {
          width: 166px;
          height: 29px;

          font-family: 'Satoshi Variable';
          font-style: normal;
          font-weight: 700;
          font-size: 24px;
          line-height: 120%;

          /* identical to box height, or 29px */
          display: flex;
          align-items: flex-end;

          color: #2B2B2B;


          /* Inside auto layout */
          flex: none;
          order: 0;
          flex-grow: 0;
        }

        .login-secondary {
          width: 162px;
          height: 24px;

          font-family: 'Satoshi Variable';
          font-style: normal;
          font-weight: 500;
          font-size: 16px;
          line-height: 150%;

          /* identical to box height, or 24px */

          color: rgba(43, 43, 43, 0.5);


          /* Inside auto layout */
          flex: none;
          order: 1;
          flex-grow: 0;
        }
      }

      .form-field {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0px;
        gap: 20px;
        /* Inside auto layout */
        flex: none;
        order: 0;
        align-self: stretch;
        flex-grow: 0;

        .field-username {
          padding: 0px;
          gap: 8px;
          width: 100%;
          height: 80px;
        }

        .field-password {
          padding: 0px;
          gap: 8px;
          width: 100%;
          height: 80px;
        }

        .login-option {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          padding: 0px;
          gap: 5px;
          height: 24px;


          /* Inside auto layout */
          flex: none;
          order: 2;
          align-self: stretch;
          flex-grow: 0;

          .option-remember {
            display: flex;
            flex-direction: row;
            align-items: center;
            padding: 0px;
          }

          .option-forgot {
            font-family: 'Satoshi Variable';
            font-style: normal;
            font-weight: 500;
            font-size: 16px;
            line-height: 150%;
            /* identical to box height, or 24px */
            text-align: center;
            color: #554ADD !important;
          }
        }
      }

      .login-button {
        padding: 0px;
        gap: 12px;
      }
    }

  }

  @media (max-width: 991px) {
    .logo-container {
      margin-left: 40px;
    }
  }
}